@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Poppins:wght@500;700&display=swap');

:root {
    --primary: #428d74;
    --secondary: #f9f8f8;
    --white: #FBFBF5;
    --black: #000000;
    --OpenSans: "Open Sans", sans-serif;
    --font-heading: "Poppins", sans-serif;
}

body {
    background-image: url("Assets/bg_test.jpg");
    background-size: cover;      /* Make the background cover the entire viewport */
    background-repeat: no-repeat; /* Prevent the image from tiling */
    background-position: center;  /* Center the background image */
    margin: 0;
    padding: 0;
    font-family: var(--OpenSans);
    min-height: 100%;
    text-align: center;
}

/* Heading */
h1 {
    color: var(--white);
    font-weight: 700;
    font-size: 2.2rem;
    margin: 0;

}

.loading-animation{
    min-width: 10rem;
    min-height: 10rem;
    margin: auto;
    margin-top: 3rem;
}

/* Navbar */
.navbar-container {
    width: 100vw;
    min-height: 4rem;
    max-height: 4rem;
    background:  var(--primary);
    text-align: center;
    border: 1px solid rgba(var(--primary), 0.2);
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
    color: var(--white);
    padding: 1rem 0;
    backdrop-filter: blur(5px) saturate(150%);
    
}

/* Contact Info Styling */
.navbar-contact {
    font-size: 0.9rem;
    font-weight: 600;
    color: var(--white);
    margin-bottom: 10px; /* Slightly more space below the contact info */
}

/* Menu Items Styling */
.navbar-menu-items {
    display: flex;
    justify-content: center;
    gap: 30px; /* Increase gap for better spacing between items */
    list-style: none;
    padding: 0;
}

.navbar-menu-items li {
    font-size: 1.1rem;
    font-weight: 500;
    color: var(--white);
    border-radius: 15px; /* Rounded corners for each item */
    transition: background-color 0.3s ease, color 0.3s ease; /* Smooth color transition */
    padding: 8px 15px; /* Add padding for more clickable area */
}

/* Hover effect for menu items */
.navbar-menu-items li:hover {
    background-color: #A7D7C5; /* Subtle background change */
    color: var(--primary); /* Text color change on hover */
}

/* Link Styling */
.navbar-menu-items a {
    color: inherit;
    text-decoration: none;
    display: block; /* Ensure the entire area of the list item is clickable */
}


/* Search Bar */
.search-container {
    display: flex;
    max-width: 80vw;
    margin: 2rem auto;
}

.search-box {
    display: flex;
    align-items: center;
    border: 2px solid var(--primary);
    border-radius: 10px;
    padding: 0;
    background-color: var(--white);
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.search-box input[type="text"] {
    border: none;
    outline: none;
    font-size: 1rem;
    margin: 0.6rem;
    color: var(--black);
    background-color: #ffffff00;
}

.search-container button {
    border: none;
    cursor: pointer;
    background-color: var(--white);
    margin: 0 0.5rem;
}
.search-container button img{
    max-height: 2rem;
}

.search-box button:hover {

    cursor: pointer;
}

/* Tips Box */
.tips-box {
    background-color: var(--white);
    border: 1px solid var(--primary);
    border-radius: 10px;
    padding: 2rem;
    max-width: 50em;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    margin: 6rem auto;
}

.tips-box h2 {
    color: var(--primary);
    font-size: 2rem;
}

.tips-box ul {
    padding: 0;
    list-style-position: inside;
    padding: 1rem 2rem;
}

.tips-box ul li {
    font-size: 1.2rem;
    margin-bottom: 1rem;
    text-align: left;
}



.home-container{
    display: grid;
    grid-template-rows: 1fr 2fr 0.5fr 1fr;
}



/* Header container */
.header-container {
    display: flex; /* Use flexbox for alignment */
    align-items: center; /* Vertically center items */
    justify-content: center; /* Center items horizontally */
    gap: 15px; /* Space between logo and heading */
    margin-top: 20px; /* Add some space above */
}

/* Logo styling */
.logo {
    width: 40px; /* Set the width of the logo */
    height: auto; /* Keep the aspect ratio */
}

/* Container for the buttons */
.button-container {
    display: flex; /* Use flexbox to arrange items horizontally */
    justify-content: center; /* Optional: center items horizontally */
    gap: 20px; /* Add spacing between buttons */
}

.itt-container img{
    max-height: 2rem;
    filter: invert(1) brightness(2);
}

.itt-container{
    display: flex;
    max-height: 4rem;
    margin: auto;
}

/* Styles for Capture Button */
.capture-button,.upload-button {
    padding: 1rem;
    margin: 0 0.5rem;
    border-radius: 10px;
    background-color: var(--black);
    transition: background-color 0.3s ease, transform 0.3s ease;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.capture-button:hover, .upload-button:hover {
    background-color: #36b1a1;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.3);
    cursor: pointer;
}


.hero-section {
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.confirm-container{
    display: grid;
    grid-template-rows: 1fr 1fr;
    justify-items: center;
    width: 100%;
}

.confirm-container img{
    margin: 2rem 1rem;
    min-height: 40vh;
    min-width: 20rem;
    object-fit: cover;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.confirm-text{
    background-color: var(--white);
    border: 1px solid var(--primary);
    min-width: 20rem;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    max-height: 12rem;
    
}


@media screen and (max-width: 600px) {
    .tips-box ul {
        padding: 0;
        list-style-position: inside;
        padding: 0;
    }

    .tips-box ul li{
        font-size: 1rem;
    }
    .tips-box {
        padding: 1rem;
        max-width: 80vw;
        margin: 6rem auto;
    }
    
    .tips-box h2 {
        font-size: 1.8rem;
    }

}